import React from 'react'
import styled, { css } from 'styled-components'
import Contacts from '../Components/Contacts'
import Canopy from '../Components/Canopy'

import { MainHeading } from './Index'

import monitor from '../Images/monitor.png'
import github from '../Images/github.png'
import calculator from '../Images/calculator.png'

export const Showcase = styled.section`
    max-width: 1140px;
    padding: 100px 20px;
    margin: auto;
    @media (max-width: 710px) { 
        padding: 50px 20px;
    }
`

export const ShowcaseImage = styled.img`
    height: 340px;
    @media (max-width: 1100px) { 
        height: 450px;
    }
    @media (max-width: 650px) { 
        height: 340px;
    }
    @media (max-width: 300px) { 
        height: 300px;
    }
`

export const ShowcaseBlock = styled.div`
    background: #F7F7F7;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 5px;
    padding: 15px;
`

export const ShowcaseWebsite = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: center;
`

export const ShowcaseWrapper = styled.div`
    display: flex; 
    justify-content: space-between;
    @media (max-width: 950px) { 
        flex-direction: column;
        text-align: center;
        align-items: center;
    }
`

export const ShowcaseContainer = styled.div`
    ${props => props.photo && css`
        max-width: 785px;
    `}

`

export const ShowcaseLogo = styled.img`
    width: 35px;
    height: 35px;
    margin-right: 15px;
`

export const ShowcaseName = styled.p`
    margin: 0;
`

export const ShowcaseButton = styled.a`
    border-radius: 100px;
    background: #000000;
    height: 35px;
    text-align: center;
    align-items: center;
    display: flex;
    padding: 0 20px;
    text-decoration: none;
    color: #FFFFFF;
`

export const Hero = styled.div`
    max-width: 1140px;
    padding: 0 40px;
    margin: 50px auto 0;
    display: flex;
    justify-content: space-between;
    @media (max-width: 1000px) { 
        display: flex;
        flex-direction: column;
    }
    @media (max-width: 710px) { 
        margin-bottom: 50px;
    }
`

export const SideHeading = styled(MainHeading)`
    margin: 0 0 25px;
    text-align: left;
    @media (max-width: 1000px) { 
        text-align: center;
        padding: 0 20px;
    }
    ${props => props.photo && css`
    @media (max-width: 800px) { 
        max-width: 400px;
    }
    @media (max-width: 550px) { 
        max-width: 250px;
    }
    `}
`

export const HeroContainer = styled.div`
    max-width: 430px;
    @media (max-width: 1000px) { 
        display: flex;
        padding: 0 40px;
        justify-content: space-between;
        max-width: none;
        flex-direction: row-reverse;
    }
    @media (max-width: 550px) { 
        padding: 0;
    }
    @media (max-width: 730px) { 
        display: flex;
        flex-direction: column;
    }
    ${props => props.heading && css`
    @media (max-width: 1000px) { 
        display: block;
        max-width: 500px;
        padding: 0;
        text-align: center;
        margin: 0 auto 50px;
    }
    `}
`

export const HeroBlock = styled.div`
    max-width: 430px;
    margin-bottom: 50px;
    &:last-child {
        margin-bottom: 0;
    }
    @media (max-width: 1000px) { 
        max-width: calc(100% / 2 - 50px);
        margin-bottom: 0;
    }
    @media (max-width: 730px) { 
        max-width: 80%;
        margin: 0 auto 50px;
    }
    @media (max-width: 650px) { 
        max-width: 100%;
        margin: 0 auto 50px;
    }
`

export const HeroImage = styled.img`
    margin-bottom: 10px;
`

export const HeroParagraph = styled.p`
    margin: 0 0 50px;
    &:last-child {
        margin: 0;
    }
    ${props => props.only_child && css`
        @media (max-width: 1100px) { 
            max-width: 600px;
        }
        &:last-child {
            margin: 0 0 50px;
            @media (max-width: 950px) { 
                margin: 0 auto 50px;
                padding: 0 20px;
            }
        }
    `}
    ${props => props.description && css`
    margin-bottom: 25px;
        @media (max-width: 950px) { 
            padding: 0 20px;
        }
    `}

`

export const HeroSubheading = styled.h3`
    font-size: 18px;
    margin: 0 0 10px;
`

const Websites = () => {
    return (
        <div className="page">
            <Hero>
                <HeroContainer heading>
                    <HeroImage src={monitor} alt="Hero Image" />
                    <SideHeading>My concepts or websites</SideHeading>
                    <HeroParagraph>Either commercial or creative</HeroParagraph>
                </HeroContainer>
                <HeroContainer>
                    <HeroBlock>
                        <HeroSubheading>Shout out</HeroSubheading>
                        <HeroParagraph>Some design elements, ideas or icons may have been taken from other designers and developers. In my work I mostly use Icons8 for icons and Behance for inspiration</HeroParagraph>
                    </HeroBlock>
                    <HeroBlock>
                        <HeroSubheading>How it works</HeroSubheading>
                        <HeroParagraph>If you want to order a custom design just contact me with the form below and I will try to satisfy all your needs. The same goes for a full website development. Everything is made individually and with precision. </HeroParagraph>
                    </HeroBlock>
                </HeroContainer>
            </Hero>
            <Canopy></Canopy>
            <Showcase>
                <ShowcaseWrapper>
                    <ShowcaseContainer calculator>
                        <HeroParagraph description>HTML + CSS + JavaScript</HeroParagraph>
                        <SideHeading>An awesome calculator to do math operations</SideHeading>
                        <HeroParagraph only_child>I wrote this calculator myself in a couple of days and can proudly say that it works without bugs just right. It can display results of summary, division, multiplying and subtraction. It can also find a square and a square root of the number or work with brackets</HeroParagraph>
                    </ShowcaseContainer>
                    <ShowcaseImage src={calculator} alt="Calculator Mockup" />
                </ShowcaseWrapper>
                <ShowcaseBlock>
                    <ShowcaseWebsite>
                        <ShowcaseLogo src={github} alt="Github" />
                        <ShowcaseName>Github</ShowcaseName>
                    </ShowcaseWebsite>
                    <ShowcaseButton href='#'>See it!</ShowcaseButton>
                </ShowcaseBlock>
            </Showcase>
            <Contacts></Contacts>
        </div >
    )
}

export default Websites